body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.pcard {
  margin: 10px;
}

.projectCard {
  background: url("https://s3.amazonaws.com/lyrics-translation-tool/assets/img/md-img1.jpg")
    center / cover;
  height: 300px;
}

.project-title {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: bold;
  color: white;
  text-shadow: 1px 1px 3px black;
}
.editable-header {
  margin-top: 200px
}

.editing-header-one {
  background-color: #00b8d4;
}

.editing-header-two {
  background-color: white;
}

.editing-header-two-title {
  color: black;
}
.normal-text {
  width: 100%;
}

.selector {
  width: 200px;
}

